'use client';
import { shuffleArray } from '@/shuffleArray';
import { ChangeEvent, useCallback, useState } from 'react';
import { unfathomableCharacters } from './characters';
import type { Character } from './Character';
import { calculateVariance } from '@/calculateVariance';

export default function UnfathomablePage() {
  const [playerCount, setPlayerCount] = useState(3);
  const [characters, setCharacters] = useState<ReadonlyArray<Character>>([]);

  const onGenerateClick = useCallback(() => {
    setCharacters(
      shuffleArray(unfathomableCharacters)
        .slice(0, playerCount)
        .sort((a, b) => a.name.localeCompare(b.name)),
    );
  }, [playerCount]);

  const onPlayerCountChange = useCallback(
    (event: ChangeEvent<HTMLInputElement>) => {
      setPlayerCount(Number(event.target.value));
    },
    [],
  );

  const totalWplyw = characters.reduce(
    (sum, char) => sum + (char.wplyw ?? 0),
    0,
  );
  const totalWiedza = characters.reduce(
    (sum, char) => sum + (char.wiedza ?? 0),
    0,
  );
  const totalPercepcja = characters.reduce(
    (sum, char) => sum + (char.percepcja ?? 0),
    0,
  );
  const totalSila = characters.reduce((sum, char) => sum + (char.sila ?? 0), 0);
  const totalWola = characters.reduce((sum, char) => sum + (char.wola ?? 0), 0);

  const variance = calculateVariance([
    totalWplyw,
    totalWiedza,
    totalPercepcja,
    totalSila,
    totalWola,
  ]);

  return (
    <main>
      <header>
        <h1>Unfathomable</h1>
      </header>
      <label>
        Player Count: {playerCount}
        <input
          type='range'
          min={3}
          max={6}
          value={playerCount}
          onChange={onPlayerCountChange}
        />
      </label>
      <button onClick={onGenerateClick}>generate</button>
      <table>
        <thead>
          <tr>
            <th>Name</th>
            <th>Wpływ</th>
            <th>Wiedza</th>
            <th>Percepcja</th>
            <th>Siła</th>
            <th>Wola</th>
          </tr>
        </thead>
        <tbody>
          {characters.map((character) => (
            <tr key={character.name}>
              <td>{character.name}</td>
              <td>{character.wplyw ?? '-'}</td>
              <td>{character.wiedza ?? '-'}</td>
              <td>{character.percepcja ?? '-'}</td>
              <td>{character.sila ?? '-'}</td>
              <td>{character.wola ?? '-'}</td>
            </tr>
          ))}
        </tbody>
        <tfoot>
          <tr>
            <td>Total</td>
            <td>{totalWplyw}</td>
            <td>{totalWiedza}</td>
            <td>{totalPercepcja}</td>
            <td>{totalSila}</td>
            <td>{totalWola}</td>
          </tr>
          <tr>
            <td>Variance</td>
            <td colSpan={5}>{variance.toFixed(2)}</td>
          </tr>
        </tfoot>
      </table>
    </main>
  );
}
